$(".phone-input").mask("+7(999) 999-9999");

// header burger
const burgerBtn = document.querySelector(".table-burger");
const nav = document.querySelector("#nav");
const overlay = document.querySelector(".overlay");

burgerBtn.addEventListener("click", openNav);

function openNav() {
  nav.classList.toggle("active");
  burgerBtn.classList.toggle("active");
  document.body.classList.toggle('stick')
}

// close navbar when click outside
window.addEventListener("click", (e) => {
  const target = e.target;
  if (target.closest("#overlay") ) {
    nav.classList.remove("active");
    document.body.classList.remove('stick')
    burgerBtn.classList.remove("active");
  }
});

// animation scroll
const observer = new IntersectionObserver((entries) => {
  entries.forEach((entry) => {
    if(entry.isIntersecting) {
      entry.target.classList.add('show');
    } else {
      // entry.target.classList.remove('show');
    }
  });
});

const hiddenEl = document.querySelectorAll(".animate");

hiddenEl.forEach((el) => observer.observe(el))